<template>
	<div id="finance-index">
		<public-header title="资产"></public-header>
		<div class="content">
			<div class="account bg-info">
				<div class="font-14 margin-15-b">账户可用余额({{Lang.dollarSignCn}})</div>
				<div>{{Lang.dollarSign}}{{account.amount}}</div>
			</div>
			<span class="icon icon-m"></span>
			<van-cell-group class="margin-10-t">
				<van-cell title="保证金" :value="Lang.dollarSign + account.deposit" >
					<div slot="icon" class="icon icon-lock margin-5-r"></div>
				</van-cell>
				<van-cell title="预存金" :value="Lang.dollarSign + account.yucunjin" >
					<div slot="icon" class="icon icon-moneybag margin-5-r"></div>
				</van-cell>
				<van-cell title="账户明细" value="" is-link :to="util.getUrl({path: 'pages/finance/current'})">
					<div slot="icon" class="icon icon-account margin-5-r"></div>
				</van-cell>
				<van-cell title="提现记录" value="" is-link :to="util.getUrl({path: 'pages/finance/getcashRecord'})">
					<div slot="icon" class="icon icon-text1 margin-5-r"></div>
				</van-cell>
				<van-cell title="预存金明细" value="" is-link :to="util.getUrl({path: 'pages/finance/yucunjin'})">
					<div slot="icon" class="icon icon-text1 margin-5-r"></div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="申请提现" value="" is-link :to="util.getUrl({path: 'pages/finance/getCash'})">
					<div slot="icon" class="icon icon-refund margin-5-r"></div>
				</van-cell>
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				account: {},
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/finance/getcash/index',
					autoAssign: true,
					variable: 'account'
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#finance-index .account{
		text-align: center;
		font-size: 30px;
		padding: 40px 0;
	}
</style>


